import React from "react";
import "./About.scss";
import { AboutItem } from "./AboutItem";
import { Lazy } from 'react-lazy';

const About = ({ children, page, id }) => {
  const imgClassName = AboutItem.background[page || "beranda"];
  const judulClassName = AboutItem.judul[page || "beranda"];
  const contentClassName = AboutItem.content[page || "beranda"];
  return (
    <div className="container" id={id}>
      <div className="about">
        <Lazy className="img-wrapper" ltIE9>
          <img
            src={imgClassName}
            className="img-about"
            data-aos="fade-up-right"
            data-aos-anchor-placement="bottom-bottom"
            alt="About"
          />
        </Lazy>
        <div className="text-wrapper" data-aos="fade-up-left">
          <div className="text-content">
            <h2>{judulClassName}</h2>
            <p>{contentClassName}</p>
            <div className="about-btn">{children}</div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default About;